import React, { Component } from 'react'
import fetchUtil from 'utils/fetchUtil'
import { fixDomain } from 'utils/domain'
import { getCookie } from 'utils/tools'
import '../H5MemberContainer.scss'

class MemberInfo extends Component {
  constructor(props) {
    super(props)
    this.state = {
      cardId: ''
    }
  }

  getMemberInfo() {
    const url = fixDomain('http://api.sit.ffan.net/bocxapi/v1/oauth/storeUser')
    fetchUtil.getJSON(url, {
      loginToken: getCookie('boc_loginToken'),
      storeId: getCookie('boc_storeId'),
    }).then(data => {
      this.setState({
        cardId: data.cardId
      })
    }).catch(err => {
      console.log(err)
    })
  }

  componentDidMount() {
    this.getMemberInfo()
  }

  render() {
    if (!this.props.basic) return null
    const { storeName, storePicsrc, storeAddress } = this.props.basic
    const { cardId } = this.state
    return <div className="memberCard-block">
      <div className="memberCard-box">
        <div className="memberCard-container">
          {this.props.url
              ? <img className="memberCard-bg" src={this.props.url} />
              : null
            }
          <dl className="memberCard-info">
            <dt className="storeLogo-img">
              <img src={`http://timg.ffan.com/convert/resize/url_${storePicsrc}/tfs/1.png`} />
            </dt>
            <dd className="store-name">{storeName}</dd>
            <dd className="store-address">{storeAddress}</dd>
          </dl>
          <p className="memberCard-tel">
            <span>{cardId.substring(0, 3)}</span>
            <span>{cardId.substring(3, 7)}</span>
            <span>{cardId.substring(7, 11)}</span>
          </p>
        </div>
        <div className="memberCard-shadow" />
      </div>
      <div className="memberCard-cardnum">
        <h5 className="title"></h5>
        <p className="card-num">
          <span>{cardId.substring(0, 3)}</span>
          <span>{cardId.substring(3, 7)}</span>
          <span>{cardId.substring(7, 11)}</span>
        </p>
      </div>
    </div>
  }
}

export default MemberInfo



