import 'whatwg-fetch'
import * as Tools from './tools'

const DEBUG_MODE = new RegExp('debug=1').test(location.search)

export const getLoginInfo = () => {
  const fieldsObject = {
    clientType: '',
    token: '',
    p1: '',
    p2: '',
    p3: '',
    p4: '',
    p5: '',
    p6: '',
    p7: '',
    p8: '',
    p9: '',
    p10: '',
    p11: '',
    p12: '',
    p13: '',
    p14: '',
    p15: ''
  }
  return Tools.getValueFromUrl(fieldsObject, location.href)
}

export const isObject = x => x && typeof x === 'object'

export const makeReject = (status, message) => Promise.reject(new Error({ status, message }))

export const buildSearchParams = (params) => Object.keys(params).map(key => {
  return [key, params[key]].map(encodeURIComponent).join('=')
}).join('&').replace(/%20/g, '+')

const defaultHeaders = {
  'Accept': 'application/json',
  // 'Content-Type': 'application/x-www-form-urlencoded'
  // 'Cache-Control': 'no-cache'
}

const defaultOptions = {
  credentials: 'include',
  mode: 'cors',
}

class FetchUtil {
  static getJSON (url, params = {}, options = {}) {
    if (!url.trim()) {
      return makeReject(-101, 'URL is empty')
    }

    if (!isObject(params) || !isObject(options)) {
      return makeReject(-102, 'The type of params and options must be a Object')
    }

    if (DEBUG_MODE) {
      params.debug = 1
    }

    let _params = Object.assign({}, getLoginInfo(), params)

    _params = buildSearchParams(_params)
    const { headers, ...rest } = options

    options = {
      method: 'GET',
      headers: {
        ...defaultHeaders,
        ...headers
      },
      ...defaultOptions,
      ...rest
    }

    if (_params) {
      const quesMark = new RegExp('\\?').test(url) ? '&' : '?'
      url = `${url}${quesMark}${_params}`
    }

    return new Promise((resolve, reject) => {
      fetch(url, options).then(res => res.json()).then(json => {
        const { status, data, msg, message } = json
        if (1 * status === 200) {
          resolve(data)
        } else {
          console.log(status + '|' + (msg || message))
          reject(json)
        }
      })['catch'](e => {
        reject(e)
      })
    })
  }

  static postJSON (url, params = {}, options = {}) {
    if (!url.trim()) {
      return makeReject(-101, 'URL is empty')
    }

    if (!isObject(params) || !isObject(options)) {
      return makeReject(-102, 'The type of params and options must be a Object')
    }

    if (DEBUG_MODE) {
      params.debug = 1
    }

    if (options['loginInfo'] !== false) {
      params = Object.assign({}, getLoginInfo(), params)
    }

    let { headers, formDataType, ...rest } = options
    let body

    headers = { ...defaultHeaders, ...headers }

    switch (formDataType) {
      case 'json':
        headers['Content-Type'] = 'application/json'
        body = JSON.stringify(params)
        break
      case 'FormData':
        const data = new FormData()
        for (const p in params) {
          if (typeof params[p] === 'object') {
            data.append(p, JSON.stringify(params[p]))
          } else {
            data.append(p, params[p])
          }
        }
        body = data
        break
      case 'file':
        body = params
        break
      case 'urlencode':
      default:
        headers['Content-Type'] = 'application/x-www-form-urlencoded'
        body = buildSearchParams(params)
    }

    options = {
      method: 'POST',
      headers,
      body,
      ...defaultOptions,
      ...rest
    }

    return new Promise((resolve, reject) => {
      fetch(url, options).then((res) => res.json()).then(json => {
        const { status, data, msg, message } = json
        if (1 * status === 200) {
          resolve(data)
        } else {
          //alert(`${status}|${msg}`)
          console.log(`${status}|${msg || message}`)
          reject(json)
        }
      })[ 'catch' ](e => {
        reject(e)
      })
    })
  }
}

export default FetchUtil



