export const isUndefined = (v) => {
  return (typeof v === 'undefined')
}

export const getValueFromUrl = (source, url) => {
  const _url = url || location.href
  const queryString = _url.split('?')[1]
  const urlParams = {}
  const result = {}

  if (queryString) {
    queryString.split('&').forEach(item => {
      const field = item.split('=')
      urlParams[field[0]] = isUndefined(field[1]) ? '' : field[1]
    })
  }

  Object.keys(source).forEach(item => {
    result[item] = isUndefined(urlParams[item]) ? source[item] : urlParams[item]
  })

  return result
}

export const getUrlParam = (key) => {
  const params = location.search.substr(1).split('&')
  let param = params.find(item => {
    return item.toUpperCase().indexOf(key.toUpperCase()) > -1
  })
  param = param ? param.split('=')[1] : ''
  return param
}

export const getCookie = (key) => {
  const cookiesArr = document.cookie.split(';')
  let obj = ''
  cookiesArr.map((item) => {
    item.split('=').map(subItem => {
      if (subItem.trim().toUpperCase() === key.toUpperCase()) {
        obj = item
      }
    })
  })
  return obj ? obj.split('=')[1] : null
}



